<template>
  <div class="container">
    <div class="row">
      <!-- <button v-on:click="listMember" id="mybutton">Go</button> -->
    </div>

    <div class="row" style="text-align:center; margin-top:30px;">
      <b style="font-size:32px; margin-bottom: 10px;">ข้อมูลสมาชิก</b>
    </div>
    <!-- 
    <div class="row" style="height:700px !important;  overflow-y: scroll !important;">-->
    <table class="table table-striped">
      <thead style="background-color: #4169E1; color:white;">
        <tr>
          <th scope="col" style="text-align:center; ">
            <b style="font-size:18px;">Member ID</b>
          </th>
          <th scope="col" style="text-align:center; ">
            <b style="font-size:18px;">Name</b>
          </th>
          <th scope="col" style="text-align:center; ">
            <b style="font-size:18px;">Phone</b>
          </th>
          <th scope="col" style="text-align:center; ">
            <b style="font-size:18px;">Address</b>
          </th>
          <th scope="col" style="text-align:center; ">
            <b style="font-size:18px;">Action</b>
          </th>
        </tr>
      </thead>

      <tbody>
        <tr v-bind:key="item.id" v-for="(item , index ) in memberList">
          <td scope="row" value="0" style="text-align:center;">{{ memberList[index].member_id }}</td>
          <td scope="row" value="0" style="text-align:left;">{{ memberList[index].aliasname }}</td>
          <td
            scope="row"
            value="0"
            style="text-align:center;"
          >{{ changePhone(memberList[index].phoneregis) }}</td>
          <td scope="row" value="0" style="text-align:left;">{{ memberList[index].address }}</td>
          <td scope="row" value="0" style="text-align:center;">
            <!-- <button>COD</button> -->
            <button type="button" class="btn btn-success" v-on:click="getDataMember(index)">COD</button>
          </td>
        </tr>
      </tbody>
    </table>
