          </div>
          <div class="modal-body" style="height:600px; ">
            <div class="row" style="margin-bottom: 20px;">
              <div class="col-sm-1"></div>
              <div class="col-sm-10">
                <div class="col-sm-2">
                  <b>รูปถ่ายหน้าบัญชี</b>
                </div>
                <div class="col-sm-10">
                  <input
                    id="fileBank"
                    type="file"
                    accept="image/*"
                    @change="onFileChangeBank"
                    ref="urlBank"
                    v-on:change="handleFileUploadBookBank"
                  />
                </div>
                <div class="col-sm-4"></div>
              </div>
              <div class="col-sm-1"></div>
            </div>

            <div class="row" style="margin-bottom: 20px;">
              <div class="col-sm-1"></div>
              <div class="col-sm-10">
                <div class="col-sm-2"></div>
                <div class="col-sm-10">
                  <div id="preview">
                    <img
                      v-img
                      class="responsive"
                      v-if="urlBank"
                      :src="urlBank"
                      style="margin-top: 0px; height:200px;width:500px;"
                    />
                  </div>
                </div>
                <div class="col-sm-4"></div>
              </div>
              <div class="col-sm-1"></div>
            </div>

            <div class="row" style="margin-bottom: 20px;">
              <div class="col-sm-1"></div>
              <div class="col-sm-10">
                <div class="col-sm-2">
                  <b>ชื่อบัญชี</b>
                </div>
                <div class="col-sm-10">
                  <input
                    v-on:keypress="inputCheckFormatName"
                    v-model="nameBank"
                    placeholder="ชื่อบัญชี"
                    class="form-control"
                    type="text"
                    maxlength="12"
                  />
                </div>
              </div>
              <div class="col-sm-1"></div>
            </div>
            <div class="row" style="margin-bottom: 20px;">
              <div class="col-sm-1"></div>
              <div class="col-sm-10">
                <div class="col-sm-2">
                  <b>เลขที่บัญชี</b>
                </div>
                <div class="col-sm-10">
                  <input
                    v-on:keypress="onlyNumber"
                    v-model="numberBank"
                    placeholder="เลขที่บัญชี"
                    class="form-control"
                    type="text"
                    maxlength="12"
                  />
                </div>
              </div>
              <div class="col-sm-1"></div>
            </div>

            <div class="row" style="margin-bottom: 20px;margin-top: 20px;">
              <div class="col-sm-1"></div>
              <div class="col-sm-10">
                <div class="col-sm-2">
                  <b>ธนาคาร</b>
                </div>
                <div class="col-sm-10">
                  <select class="form-control dropdown-panel-detail" v-model="codeBank">
                    <option disabled selected>เลือกธนาคาร</option>
                    <option
                      v-bind:key="item.id"
                      v-for="(item  , index) in dataBank"
                      v-bind:value="{ id: dataBank[index].id, text: dataBank[index].bankTh }"
                    >{{ dataBank[index].bankTh }}</option>
                  </select>
                </div>
              </div>
              <div class="col-sm-1"></div>
            </div>

            <div class="row" style="margin-bottom: 20px;margin-top: 20px;">
              <div class="col-sm-1"></div>
              <div class="col-sm-10">
                <div class="col-sm-4"></div>
                <div class="col-sm-5"></div>
                <div class="col-sm-3">
                  <button
                    data-dismiss="modal"
                    style="padding-bottom: 0px;margin-bottom: 0px;height: 52px;padding-top: 0px;"
                    type="button"
                    class="btn btn-success btn-lg form-control"
                    v-on:click="btnSaveMember"
                  >บันทึก</button>
                </div>
              </div>
              <div class="col-sm-1"></div>
            </div>
          </div>
          <!-- <div class="modal-footer"></div> -->
        </div>
      </div>
    </div>

    <!-- Modal HTML -->
    <div id="myModal" class="modal fade">
      <div class="modal-dialog modal-confirm">
        <div class="modal-content">
          <div class="modal-header">
            <div class="icon-box">
              <img src="../assets/img/checked-symbol.png" height="35px" style=" margin-top: 15px;" />
            </div>
            <h4 class="modal-title">บันทึกข้อมูลสำเร็จ</h4>
          </div>
          <div class="modal-body">
            <!-- <p class="text-center">Your booking has been confirmed. Check your email for detials.</p> -->
          </div>
          <div class="modal-footer">
            <button class="btn btn-success btn-block" data-dismiss="modal">OK</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Vue from "vue";
const axios = require("axios");
export default {
  data: function() {
    return {
      memberList: [],
      memberId: "",
      aliasname: "",
      address: "",
      nameBank: "",
      numberBank: "",
      phonemember: "",
      codeBank: "เลือกธนาคาร",
      url: null,
      urlBank: null,
      imgBookBank: "",
      dataBank: []
    };
  },
  mounted: function() {
    // $("#mybutton").click();
    // this.listMember();
  },
  created() {
    // setTimeout(this.listMember, 1000);
    this.listMember();
  },
  methods: {
    listMember() {
      var that = this;
      const data = {
        branch_id: 38
      };

      axios
        .post(
          "https://apidev.whatitems.com/parcel/list/member/api",
          JSON.stringify(data)
        )
        .then(function(response) {
          console.log(response.data.phonechange);
          that.memberList = response.data;
          that.getDataBank();
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    changePhone(phone) {
      var phonechange = phone.replace("66", "0");
      return phonechange;
    },
    editMember() {
      $("#updateModal").modal();
    },
    handleFileUploadBookBank() {
      this.fileBank = this.$refs.urlBank.files[0];
      this.uploadImg = this.$refs.urlBank.files[0];
      // console.log(this.$refs.urlBank.files[0].name);
    },
    onFileChangeBank(e) {
      const fileBank = e.target.files[0];
      this.urlBank = URL.createObjectURL(fileBank);
    },
    upLoadImgBookBank() {
      let formDataBookBank = new FormData();
      formDataBookBank.append("file", this.fileBank);

      function main() {
        return axios
          .post("https://api.945cs.work/upload_frontend", formDataBookBank, {
            headers: {
              "Content-Type": "multipart/form-data"
            }
          })
          .then(data => {
            return data.data;
          });
      }
      main().then(data => {
        this.imgBookBank = data.fileName;
      });
    },
    getDataBank(index) {
      axios
        .get("https://apidev.whatitems.com/parcel/agent/bank/name")
        .then(resultsDataBank => {
          this.dataBank = resultsDataBank.data;
          console.log("databank", this.dataBank);
        })
        .catch(error => {
          // console.log(error);
        });
    },
    inputCheckFormatName($event) {
      var keyCode = $event.keyCode ? $event.keyCode : $event.which;
      if (!(keyCode < 48 || keyCode > 57)) {
        $event.preventDefault();
      }
    },
    onlyNumber($event) {
      //console.log($event.keyCode); //keyCodes value
      let keyCode = $event.keyCode ? $event.keyCode : $event.which;
      if (keyCode < 48 || keyCode > 57) {
        // 46 is dot
        $event.preventDefault();
      }
    },
    getDataMember(index) {
      var that = this;
      $("#updateModal").modal();
      that.memberId = this.memberList[index].member_id;
      that.aliasname = this.memberList[index].aliasname;
      that.phonemember = this.memberList[index].phoneregis;
      that.address = this.memberList[index].address;
      console.log("list", index);
      console.log("id", that.memberId);
      console.log("name", that.aliasname);
      console.log("phone", that.phonemember);
      console.log("address", that.address);
    },
    btnSaveMember() {
      console.log("upLoadImgBookBank");
      let formDataBookBank = new FormData();
      formDataBookBank.append("file", this.fileBank);
      function main() {
        return axios
          .post("https://api.945cs.work/upload_frontend", formDataBookBank, {
            headers: {
              "Content-Type": "multipart/form-data"
            }
          })
          .then(data => {
            return data.data;
          });
      }
      main().then(data => {
        this.imgBookBank = data.fileName;
        console.log("imgBookBank", this.imgBookBank);
        this.dataSave();
      });
    },
    dataSave() {
      console.log("dataSave");
      var that = this;
      const datamember = {
        phoneRegis: that.phonemember,
        address: that.address,
        bankAccName: that.nameBank,
        bankIssue: that.codeBank.text,
        bankAcc: that.numberBank,
        imgBookBankUrl: that.imgBookBank
      };
      console.log("datamember", datamember);

      axios
        .post(
          "https://apidev.whatitems.com/parcel/agent/update/cod/register/api",
          JSON.stringify(datamember)
        )
        .then(function(response) {
          console.log(response.data.status);
          if (response.data.status == "SUCCESS") {
            $("#updateModal").modal("hide");
            $("#myModal").modal();
            that.fileBank = null;
            that.uploadImg = null;
            that.urlBank = null;
            that.imgBookBank = null;
            that.numberBank = "";
            that.nameBank = "";
            that.codeBank.text = "";

            // $("#updateModal").hide();
          }
        })
        .catch(function(error) {
          console.log(error);
        });
    }

    // btnSaveMember() {
    //   console.log("Save");
    //   this.upLoadImgBookBank();

    //   var that = this;
    //   const datamember = {
    //     phoneRegis: that.phonemember,
    //     address: that.address,
    //     bankAccName: that.nameBank,
    //     bankIssue: that.codeBank.text,
    //     bankAcc: that.numberBank,
    //     imgBookBankUrl: that.imgBookBank
    //   };
    //   console.log("datamember", datamember);

    //   axios
    //     .post(
    //       "https://apidev.whatitems.com/parcel/agent/update/cod/register/api",
    //       JSON.stringify(datamember)
    //     )
    //     .then(function(response) {
    //       console.log(response.data.status);
    //     })
    //     .catch(function(error) {
    //       console.log(error);
    //     });
    // },

    // upLoadImgBookBank() {
    //   console.log("upLoadImgBookBank");
    //   let formDataBookBank = new FormData();
    //   formDataBookBank.append("file", this.fileBank);
    //   function main() {
    //     return axios
    //       .post("https://api.945cs.work/upload_frontend", formDataBookBank, {
    //         headers: {
    //           "Content-Type": "multipart/form-data"
    //         }
    //       })
    //       .then(data => {
    //         return data.data;
    //       });
    //   }
    //   main().then(data => {
    //     this.imgBookBank = data.fileName;
    //     console.log("imgBookBank", this.imgBookBank);
    //   });
    // }
  }
};
</script>
